/* Подключение шрифтов */

@font-face {
	font-family: 'Roboto';
	src: url('../fonts/Roboto-Regular.eot');
	src: url('../fonts/Roboto-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/Roboto-Regular.woff') format('woff'), url('../fonts/Roboto-Regular.ttf') format('truetype');
	font-style: normal;
	font-weight: normal; }

// подключаем CSS-сброс
@import "reset";

/* Шрифты, базовые параметры и ClearFix */

html {
	background-color: #ffd;
	font: 18px/1.45 Roboto, sans-serif; }

button:active {
	transform: scale(0.98); }

::selection {
	color: #a54;
	background: #ffd; }

.wrapper {
	min-height: calc(100vh - 140px); }

.container {
	max-width: 97.5%; }

.clearfix::after {
	content: "";
	display: table;
	clear: both; }

/* Подключаем переменные */

@import "vars";

/* Блок хедера */

.header {
	background-color: #ccf; }

.inner-header {
	font-size: $_42px;
	text-align: center;
	padding: 10px 0; }

/* Блок контентной части */

.main {
	margin-top: 30px;
	margin-bottom: 40px; }

.text-cols {
	background-color: #a54;
	padding: 0 5px; }

.text-cols .text-cols__content {
	background-color: #a54;
	padding: 10px 5px; }

.text-cols__inner-content {
	background-color: #ccf;
	padding: 30px; }

/* Блок футера */

.footer {
	height: 100px;
	background-color: #ccf; }

.inner-footer {
	font-size: $_42px;
	text-align: center;
	line-height: 100px; }

// подключаем анимации
@import "animations";

// подключаем медиа-запросы
@import "media-queries";
